.aaths-main {
    background-color: rgba(0, 0, 0, 0.4);
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 10000;
}
.aaths-container {
	font-family: "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue", Helvetica, Arial, "Lucida Grande", sans-serif;
	position: relative;
	background:#ffffff;
	padding:0.25em;
	padding-left: 2.8em;
	min-height: 2.618em;
	padding: 17px;
	border-radius: 5px;
	text-align: center;
	position: fixed;
	left: 5%;
	right: 5%;
	top: 25%;
}

.aaths-instructions {
	font-size: 2em;
}

.aaths-close {
    font-family: 'Open Sans','Helvetica Neue',Helvetica,Arial,sans-serif;
    vertical-align: middle;
    color: #fff;
    background: #D0D0D0;
    color: #fff;
    border: none;
    box-shadow: none;
    font-size: 17px;
    font-weight: 500;
    -webkit-border-radius: 4px;
    border-radius: 5px;
    padding: 10px 32px;
    margin: 26px 5px 0;
    cursor: pointer;
}

@media (min-width: 600px) {
  .aaths-instructions {
    text-align: center;
  }
}
